/* reset */
html, body, div, header, footer, menu, nav, main, section, article, aside, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, canvas, details, embed, figure, figcaption, hgroup, output, ruby, summary, time, mark, audio, video, input,textarea, select, button
{ margin: 0; padding: 0; border: 0; vertical-align: baseline; box-sizing:border-box; -webkit-text-size-adjust: none; font-size:inherit; word-break:keep-all;}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {display: block;}
ol, ul {list-style: none;}
blockquote, q {quotes: none;}
blockquote:before, blockquote:after, q:before, q:after {content:""; content: none;}
table {border-collapse: collapse; border-spacing: 0; }
table th{text-align:left;}
table td, 
table th{word-break:break-all; word-wrap: break-word;}
form, fieldset{border:none;}
img{vertical-align:top; border:none; max-width:100%;}
input, select, textarea, button{vertical-align:middle; -webkit-appearance:none; appearance:none;}
textarea{resize:none; outline:none}
select::-ms-expand {display:none}
legend, caption{visibility:hidden; overflow:hidden; width:0; height:0; font-size:0; line-height:0; margin:0; padding:0;}
h1, h2, h3, h4, h5, h6{font-weight:normal; font-style:normal; }
em, address{font-weight:normal; font-style:normal; }
hr{display:none; }
button{vertical-align:middle; cursor:pointer;border:none; background-color:transparent; outline:none; color:inherit;}
.blind{ visibility:hidden; position:absolute; font-size:0; width:0; height:0; line-height:0; margin:0; padding:0; overflow:hidden}

a{color: #111; text-decoration:none;}
a:hover{ text-decoration:none;} 

html, body{width:100%; height:100%; min-width:320px;}
html {font-size:10px;}
/* 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif */
body, a, button, input, select, textarea{font-family:helvetica, arial, sans-serif;}  
body{font-size:1.5rem; font-weight:400; line-height:1.6; background:#fff; color:#111; overflow-y: scroll;} 
