.container {margin:0 auto; width:min(1024px, 100%); padding:0 5%;}
header {padding:3rem 0;}
main {padding:4rem 0 10rem; min-height:calc(100vh - 16rem);}
section {margin-top:4rem;}
footer {padding:1.5rem 0; background:#eeeeee; text-align:right;}
h1 {font-weight:bold; font-size:3.2rem;}
h2 {font-size:2rem; font-weight: bold; margin-bottom:3rem;}
nav {display: flex; justify-content: space-between; font-size:1.6rem; align-items: center;}
nav a {display:inline-block;}
nav .logo img {width:4.5rem;}
nav menu {position:relative; overflow:hidden;}
nav menu a {position:relative; padding:0.6rem 1.5rem; text-transform: capitalize;  transition:all .5s;}
nav menu a span {display: inline-block; transition:all .5s;}
nav .active span {transform: scale(1.15); transform-origin: bottom center; }
.tags {display:flex; justify-content:flex-start; gap:3px; flex-wrap:wrap;}
.tag {display:inline-block; padding:2px 10px; border-radius:30px; background: #666666; color:#fff;}
.hov-btn {transition:all .3s;}

/* input */
input {height: 4rem; }
textarea {height: 12rem;}
input, textarea {border:1px solid #eee; width:100%; padding: 0.9rem 1.2rem; font-size: 1.5rem; border-radius:4px;}
input[type="submit"] {border:0;}
/* make sure to set some focus styles for accessibility */
:focus {outline: 0;}
/* 크롬 자동완성 스타일 초기화 */
input:-webkit-autofill {-webkit-box-shadow: 0 0 0 1000px white inset; box-shadow: 0 0 0 1000px white inset;}