/* cover */
.msg-cover {position:fixed; width:100%; height:100%; top:0; left:0; background:#fff; display:flex; justify-content:center; align-items: center; z-index:10;}
.msg-cover.none {background-color: rgba(255,255,255,1);}
.msg-cover.loading {background-color: rgba(255,255,255,0.93);}

/* --------------contact-------------- */
.contact article {width:min(500px, 100%); margin-inline: auto;}
.contact .desc {margin-bottom:3rem;}
.contact .row ~ .row {margin-top:3rem;}
.contact .row ~ .row {margin-top:3rem;}
.contact label {display:block; margin-bottom:1rem; font-size:1.5rem;}
.submit {text-align:center;}
.submit input {width:15rem; cursor:pointer; background:#111; color:#fff; margin-inline: auto;}


/* --------------intro-------------- */
.intro {margin-top:0; min-height:calc(100vh - 30rem); display:flex; flex-direction: column; gap:3.4rem; align-items:center; justify-content: center;}
.intro .title {font-size:3.6rem; animation:vanishIn .5s .5s ease both; }
.intro b {position:relative; display:inline-block; letter-spacing: 0.3rem; background: linear-gradient(
	45deg,
	rgba(0,0,0, 1) 45%,
	rgba(255, 255, 255, 0.8) 50%,
	rgba(0,0,0, 1) 55%,
	rgba(0,0,0, 1) 100%
);
background-position: -13%;
-webkit-background-clip: text;
color: transparent;
animation: shine 7s 2s infinite;
background-size: 200%;}
.intro .desc {font-size:1.6rem; text-align: center; animation:vanishIn .5s 1s ease both;}
.intro .btn {display:flex; align-items: center; gap:1rem; animation:titleAnim .5s 1.5s ease both; padding:0 1.2rem;}
.intro .btn img{animation:btnAnim .5s 3s ease infinite alternate;}

@keyframes btnAnim {
	100%{
		transform:translateX(50%)
	}
}

@keyframes shine {
	0% {
		background-position: 45%;
	}
	10% {
		background-position: -218%;
	}
	100% {
		background-position: -218%;
	}
}

@keyframes titleAnim {
	0% {
		clip-path: polygon(0 100%, 100% 0, 100% 0, 0 100%);
		opacity:0;
		transform:translateY(-50px);
	}
	100% {
		clip-path: polygon(100% 100%, 100% 0, 0 0, 0 100%);
		opacity:1;
		transform:translateY(0px);
	}
}
@keyframes vanishIn {
	0% {
	  opacity: 0;
	  transform-origin: 50% 50%;
	  transform: scale(2, 2);
	  filter: blur(90px);
	}
	100% {
	  opacity: 1;
	  transform-origin: 50% 50%;
	  transform: scale(1, 1);
	  filter: blur(0px);
	}
  }


/* --------------language-switch-------------- */
.switch-wrap {display:flex; gap:1rem; align-items: center; position: fixed; bottom: 7rem; right: 3rem; background: #fff; z-index: 10; padding: 1.8rem; border-radius: 1.5rem; box-shadow: 0 0 10px rgba(0,0,0,.2);}
.switch-wrap span {font-size:1.2rem; font-weight:normal;}
.switch {display:block; width:44px; height:23px; background:#4bac4f; border-radius:30px; padding:1px;}
.switch .circle {display:block; width:50%; height:100%; border-radius:50%; background:#e8f5e9; transition: all .3s; transform: scale(1.3);}
.switch.on .circle {margin-left:50%;}

/* --------------project-------------- */
.pj {display:flex; justify-content:flex-start; flex-wrap:wrap; gap:20px;}
.pj li {width:calc((100% - 20px)/2); border-radius:15px; overflow:hidden; box-shadow: 0px 0px 15px rgba(0,0,0,.2);}
.pj a {display:block; width:100%; padding-top:70%; background-size: cover; background-position: center top; position:relative;}
.pj a img {position:absolute; top:0; left:0; width:100%;}
.pj a .cover {position:absolute; width:100%; height:100%; background:#000; opacity:0; top:0; left:0; transition: opacity .5s;}
.pj a .desc {position:absolute; width:100%; padding:0 max(6%, 15px) max(6%, 15px); left:0; bottom:0; color:#fff; opacity:0; transform:translateY(30%); transition: all .5s;}
.pj .desc .pro-tit {margin-bottom:20px; font-weight:bold; font-size:2.4rem;}

/* --------------modal-------------- */
.modal-cover {position:fixed; width:100%; height:100%; top:0; left:0; background-color:rgba(0,0,0,0.7); z-index:10;}
.modal-wrap {height:100%; background:#fff; position:absolute; top:0; right:0; width:min(570px, calc(100% - 100px)); animation: Modal 0.5s ease-in-out; will-change: transform;}
.modal {position:relative; padding:min(30px, 7%) min(30px, 7%) 100px;     overflow-y: scroll; height: 100%;}
.modal .close {position:absolute; top:2rem; right:2rem;}
.modal .close img {width:3rem;}
.modal dt {font-size:1.8rem; margin-top:3.2rem; font-weight:bold;}
.modal dd {/* font-size:1.5rem;  */margin-top:1.5rem;}
.bar {position:fixed; width:100%; bottom:0; left:0; padding:0 2rem; box-shadow: 0px 0px 15px rgba(0,0,0,.2); background:#fff; display:flex; justify-content:space-between;}
.bar button {padding:1.8rem 0; display: flex; align-items: center;}
.bar span {vertical-align:middle; padding: 0 1rem;}
.visit {display:inline-block; padding:1rem 3rem; position:relative; background:#111; border-radius:5px; color:#fff;}
.visit:after {content:''; display:inline-block; border-left:7px solid currentColor; border-top:5px solid transparent; border-bottom:5px solid transparent; margin-left:8px;}

.img-wrap {width:100%; margin:0 auto; padding-top:60%; border-radius: 15px; background-position-x: center; background-position-y: 0%; background-repeat: no-repeat; background-size: cover; box-shadow: 0px 0px 15px rgba(0,0,0,.2); transition: all .5s;}
.img-wrap.run {animation:imgMove 7s 1s ease-in-out alternate infinite;}

/* --------------about-------------- */
.about {display:flex; gap:10rem;}
.about article {flex:1;}
.category {position:relative; padding-left:2.5rem;}
.category:before {content:''; position:absolute; width:1px; height:calc(100% - 1rem); background:#ddd; top:1rem; left:0.4rem;}
.category dt {font-size:1.7rem; position:relative;}
.category dt:before {content:''; position:absolute; width:1.5rem; height:1.5rem; box-sizing:border-box; border:0.2rem solid currentColor; background:#fff; border-radius:50%; top:0.6rem; left:-2.8rem;}
.category dd {margin-top:1.5rem;}
.category dd + dt {margin-top:2.5rem;}
.category .tag {font-size: 1.4rem;}



/*@media all and (min-width: 769px){*/
@media (hover: hover) {
	.pj a:hover .cover {opacity:0.7;}
	.pj a:hover .desc {opacity:1; transform:translateY(0);}

	.hov-btn:hover {transform:scale(1.1);}
}/* only PC */

@media screen and (max-width:768px) {
	html {font-size:9px;}
	.pj {gap:15px;}
	.pj a .cover {opacity:0.7;}
	.pj a .desc {opacity:1; transform:translateY(0);}

	.about {flex-direction: column; gap:7rem;}
}/* (max-width:768px) */

@media screen and (max-width:480px) {
	.pj li {width:100%;}

	.modal-wrap {width:100%;}
}/* (max-width:480px) */

/* ------------------------------------------ */


@keyframes Modal {
	0% {
		transform:translateX(100%)
	}
	100% {
		transform:translateX(0)
	}
}

@keyframes imgMove {
	0% {
		background-position-y: 0%;
	}
	2% {
		background-position-y: 0%;
	}
	98% {
		background-position-y: 100%;
	}
	100% {
		background-position-y: 100%;
	}
}